<template>
    <div class="sub3">
        <h2>저염실천음식점</h2>
        <!-- <v-app id="inspire">
          <div class="tab">
              <v-card>
                <v-tabs
                  v-model="tab"
                  background-color="primary"
                  dark
                >
                  <v-tab
                    v-for="item in items"
                    :key="item.tab"
                  >
                    {{ item.tab }}
                  </v-tab>
                </v-tabs>

                <v-tabs-items v-model="tab">
                  <v-tab-item
                    v-for="item in items"
                    :key="item.tab"
                  >
                    <v-card flat>
                      <v-card-text>{{ item.content }}</v-card-text>
                    </v-card>
                  </v-tab-item>
                </v-tabs-items>
              </v-card>
          </div>
        </v-app> -->
        <div class="inputBox">
            <label for="title" class="hidden">지역 </label>
            <input id="title" type="text" v-model="value" placeholder="지역을 입력하세요."/>
            <button @click="search()">검색</button>
        </div>

        <div v-show="message" style="text-align:center; height: 30px">{{msgtext}}</div>

        <div class="storeBox">
        <div v-for="(x,i) in naReduce" :key="i" class="store">
            <div class="left">
            <span>{{ [i+1] + '. ' + x.BSSH_NM }}</span>
            <p>{{ '- 인허가번호 : ' + x.LCNS_NO}}</p>
            <p>{{ '- 기관명 : ' + x.INSTT_NM }}</p>
            </div>
            <p class="right">{{ x.SITE_ADDR }}</p>
        </div>
        </div>
</div>
</template>


<script>
  import jdata from '../naReduce.json';

  export default {
    data(){
      return{
        message: true,
        msgtext:'',
        value: '',
        naReduce : jdata
        // tab: null,
        // items: [
        //   { tab: '서울특별시', content: 'Tab 1 Content' },
        //   { tab: '인천광역시', content: 'Tab 2 Content' },
        //   { tab: '경기도', content: 'Tab 3 Content' },
        //   { tab: '강원도', content: 'Tab 4 Content' },
        //   { tab: '충청남도', content: 'Tab 5 Content' },
        //   { tab: '충청북도', content: 'Tab 6 Content' },
        //   { tab: '세종특별자치시', content: 'Tab 7 Content' },
        //   { tab: '대전광역시', content: 'Tab 8 Content' },
        //   { tab: '전라북도', content: 'Tab 9 Content' },
        //   { tab: '전라남도', content: 'Tab 10 Content' },
        //   { tab: '광주광역시', content: 'Tab 10 Content' },
        //   { tab: '경상북도', content: 'Tab 10 Content' },
        //   { tab: '대구광역시', content: 'Tab 10 Content' },
        //   { tab: '울산광역시', content: 'Tab 10 Content' },
        //   { tab: '경상남도', content: 'Tab 10 Content' },
        //   { tab: '부산광역시', content: 'Tab 10 Content' },
        //   { tab: '제주특별자치도', content: 'Tab 10 Content' },
        // ],
      }
    },
    methods:{
      search: function(){
        let text = this.value;
        //let length = this.naReduce.length;
        if(text){
          this.naReduce = jdata.filter(function(element){
            return element.INSTT_NM.includes(text);  //조건에 참이면 객체를 배열로 리턴
          });
          
          if(this.naReduce.length==0){
             this.message=true;
             this.msgtext='검색 데이터가 없습니다';
             
          }else{
             this.message=true;
             this.msgtext=this.naReduce.length + '개가 검색되었습니다.';
             
          }
        }else{
          alert('검색어를 입력해주세요!');
        } 
      }
    }
  }
  </script>

<style>

button, input, select, textarea {
  padding: 10px;
  background-color: transparent;
  border: 1px solid #333;
}

.inputBox {
    margin: 30px auto;
    width: fit-content;
    position: relative;
}

.inputBox input#title {
  width: 25em;
  border-radius: 30px;
  padding: 10px 20px;
  border: 1px solid #18a5a0;
  background: #18a5a0;
  color: #fff;
  outline: none;
}

.inputBox input#title::placeholder {
  color: #fff
}

.inputBox button {
    position: absolute;
    right: 10px;
    border: none;
    color: #fff
}
.storeBox {
    border-top: 1px solid;
    width: 90%;
    margin:10px auto 0
}

.store {
    margin: 2em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1em 1.5em;
    line-height: 1.8em;
    border-bottom: 1px solid #ccc;
}

.store .left, .store .right {
    width: 50%;
}

.store .left span {
    font-weight: 600;
    font-size: 1.3rem;
    display: inline-block;
    margin-bottom: 10px;
}

.store .right {
  text-align: right;
}

