<!-- 서브 페이지 마크업 -->
<template lang="html">
    <div class="sub2">
        <h2 class="hidden">실천방법</h2>
        <div class="way">
          <h3>나트륨 줄이는 3가지 방법</h3>
          <div class="box">
            <v-row>
              <v-col v-for="(n,i) in 3" :key="n" cols="6" sm="4" class="d-flex">
                <img :src="img[i]">
                <p>{{text[i]}}</p>
              </v-col>
            </v-row>
          </div>
        </div>
        <v-app id="inspire">
        <div class="reduce">
          <h3>나트륨 배출을 도와주는 식품</h3>
           <v-card>
              <v-toolbar
                flat
                color="primary"
                dark
              >
                <v-toolbar-title>나트륨 배출을 도와주는 3가지 영양소</v-toolbar-title>
              </v-toolbar>
              <v-tabs vertical>
                <v-tab>
                  <v-icon left>
                    mdi-check-bold
                  </v-icon>
                  칼륨
                </v-tab>
                <v-tab>
                  <v-icon left>
                    mdi-check-bold
                  </v-icon>
                  마그네슘
                </v-tab>
                <v-tab>
                  <v-icon left>
                    mdi-check-bold
                  </v-icon>
                  칼슘
                </v-tab>

                <v-tab-item>
                  <v-card flat>
                    <v-card-text>
                      <ul class="arr">
                        <li v-for="x in k" :key="x.id">
                          <img :src="require(`@/assets/${x.image}`)" alt="">
                          <p>{{x.name}}</p>
                        </li>
                      </ul>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
                <v-tab-item>
                  <v-card flat>
                    <v-card-text>
                      <ul class="arr">
                        <li v-for="x in ma" :key="x.id">
                          <img :src="require(`@/assets/${x.image}`)" alt="">
                          <p>{{x.name}}</p>
                        </li>
                      </ul>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
                <v-tab-item>
                  <v-card flat>
                    <v-card-text>
                      <ul class="arr">
                        <li v-for="x in ca" :key="x.id">
                          <img :src="require(`@/assets/${x.image}`)" alt="">
                          <p>{{x.name}}</p>
                        </li>
                      </ul>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
              </v-tabs>
            </v-card>
        </div>
      </v-app>
      </div>
  </template>
  
  <script>
  import kdata from '../k.json';
  import cadata from '../ca.json';
  import madata from '../ma.json';

  export default {
    data() {
      return{
        k : kdata,
        ca : cadata,
        ma : madata,
        text: ["소금대신 천연향신료","국물은 적게 먹기","외식할 땐 소스따로 주문"],
        img: [require("../assets/way1.jpg"),require("../assets/way2.jpg"),require("../assets/way3.jpg")]
      }
    }
  }
  </script>
  
  <style>
    .sub2 {
      font-family: "Noto Sans KR", sans-serif;
      width: 80%;
      margin: 0 auto;
      padding: 50px 0;
      box-sizing: border-box;
    }

    .way {
      margin-bottom: 70px;
  }
     .way h3, .reduce h3 {
      font-size: 3rem;
      line-height: 1.5em;
     }

     .way .row p {
        font-size: 1.3em;
        text-align: center;
        margin-top: 20px;
        font-weight: 500;
    }

    .way .box,  .reduce {
        width: 96%;
        margin: 2rem auto 0;
    }

    .reduce {
      margin-top: 70px;
    }

    .reduce .v-card {
      margin-top: 30px;
    }

    .reduce .v-tab {
        justify-content: left;
    }

    ul.arr {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      text-align: center;
      font-size: 1.3em;
      font-weight: 600;
    }

    ul.arr li {
        width: 20%;
        margin: 0 6%;
    }

    .v-application ul.arr li p {
        margin: 20px;
    }
    ul.arr img {
        width: 70%;
    }
  </style>