<!-- 서브 페이지 마크업 -->

<template lang="html">
    <div class="sub1">
      <h2 class="hidden">나트륨줄이기 정책</h2>
      <dl class="about">
        <dt>
          <h3>나트륨 줄이기 실천 음식점?</h3>
        </dt>
        <dd>
          <p>음식점에 제공하는 메뉴의 나트륨 함량을 줄여 제공하는 음식점으로<br>
          2015년부터 식품의약안전처에서 지정하고 있어요!</p>
          <p><strong>지정기준</strong> : 
          10% 이상 나트륨을 저감하여 <span>1인분량 나트륨이 1,300mg 미만인 메뉴를 전체 메뉴의 20% 이상 운영</span>하는 음식점</p>
          <p>*나트륨이 적은 음식점은 <span>아래의 현판</span>을 통해 확인이 가능해요!</p>
        </dd>
        <dd>
          <img src="../assets/outside.jpg" alt="현판 이미지">
          <img src="../assets/outside2.jpg" alt="현판 이미지2">
        </dd>
      </dl>

      <div class="recommend">
          <h3>나트륨 섭취량</h3>
          <p>세계보건기구(WHO)에서는 1일 나트륨 섭취량을 하루 소금 섭취량 5g(약 소금 2작은술에 해당), 2000mg으로 권장하고 있어요.  </p>
          <p>2019년 기준, 국내 하루 나트륨 섭취량은 3289mg로 권장량의 1.6배 이상을 섭취하고 있답니다. 나트륨 과다 섭취 시, 뇌졸중, 고혈압, 위장병, 골다공증 등 다양한 질환에 노출될 위험이 높기 때문에 조절할 필요가 있겠죠!</p>
          <div class="box">
              <v-row>
                <v-col v-for="(n,i) in 3" :key="n" cols="6" sm="4" class="d-flex">
                  <img :src="img[i]">
                  <p>{{text[i]}}</p>
                </v-col>
              </v-row>
          </div>
      </div>

      <div class="process">
        <h3>실천음식점 지정절차</h3>
        <v-app id="inspire">
        <v-stepper v-model="e1">
          <v-stepper-header>
            <v-stepper-step
              :complete="e1 > 1"
              editable
              step="1"
            >
              실천음식점 공고 및<br>사전운영 음식점 선정
            </v-stepper-step>

            <v-divider></v-divider>

            <v-stepper-step
              :complete="e1 > 2"
              editable
              step="2"
            >
              사전운영<br>(선정 후 1개월 이상)
            </v-stepper-step>

            <v-divider></v-divider>

            <v-stepper-step :complete="e1 > 3" editable step="3">
              현장점검 및 시정조치
            </v-stepper-step>

            <v-divider></v-divider>

            <v-stepper-step step="4" editable>
              실천음식점 지정
            </v-stepper-step>

          </v-stepper-header>

          <v-stepper-items>
            <v-stepper-content step="1">
              <v-card
                class="mb-12"
                color="grey lighten-3"
              >
              <ul>
                <li>
                  <dl>
                    <dt>
                      • 사전운영 음식점 신청 접수
                    </dt>
                    <dd>
                      - 신청서류 (사전운영신청서 , 개인정보 수집 및 이용동의서)를 접수
                    </dd>
                  </dl>
                </li>
                <li>
                  <dl>
                    <dt>• 사전운영 음식점 선정 및 통보</dt>
                    <dd class="standard">
                      <span>사전운영 음식점 선정 기준</span>
                      <p>[영업주의 의지] 판매 메뉴의 나트륨 줄인 조리법 개발 및 판매에 대한
영업주의 자발적 의지</p>
                      <p>[나트륨 줄인 메뉴] 전체 판매 메뉴 중 20% 이상에 대한 나트륨 저감 메뉴
제공 여부</p> 
                      <p>
                        [염도 관리] 월 평균 주 1회 이상 염도 측정 및 기록 관리 가능 여부
                      </p>
                    </dd>
                  </dl>
                </li>
              </ul>
              </v-card>

              <v-btn
                color="primary"
                @click="e1 = 2"
              >
                Continue
              </v-btn>

              <v-btn text>
                Cancel
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="2">
              <v-card
                class="mb-12"
                color="grey lighten-3"
              >
                <dl style="padding: 0 20px">
                  <dt>
                   • 사전운영 메뉴 기초자료 수집 및 관리자 교육
                  </dt>
                  <dd style="margin-bottom: 15px">
                    <p>- 1인 분량(총중량, 가식부 중량) 및 염도 측정</p>
                    <p>- 염도측정도구 세트 지원, 운영·관리 기준 및 염도측정 방법 안내</p>
                  </dd>
                  <dd class="menu">
                    <span>나트륨 줄인 메뉴 선정 기준</span>
                    <p>전체 메뉴의 20%를 나트륨 줄인 메뉴로 선정</p>
                    <p>별도의 조리 없이 제공되는 메뉴(회, 고기 등), 계절메뉴, 메뉴판에 없는 메뉴,
정식에 포함되어 있는 개별 메뉴 등은 제외</p>
                  </dd>
                </dl>

              </v-card>

              <v-btn
                color="primary"
                @click="e1 = 3"
              >
                Continue
              </v-btn>

              <v-btn text>
                Cancel
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="3">
              <v-card
                class="mb-12"
                color="grey lighten-3"
              >
                <ul>
                  <li>
                    <dl>
                      <dt>
                        • 사전운영 현장 평가 및 미흡사항 개선요구
                      </dt>
                      <dd>
                        - 필수항목 적합 및 총점 70점 이상 지정 유지 (현장점검 결과는 식약처와 공유)
                      </dd>
                    </dl>
                  </li>
                  <li>
                    <dl>
                      <dt>• 현장평가 기준미달 음식점은 최소 1개월 사전운영 후 재평가 실시</dt>
                      <dd>
                        - 개선요구사항 조치결과 확인 등
                      </dd>
                    </dl>
                  </li>
                </ul>
              </v-card>

              <v-btn
                color="primary"
                @click="e1 = 4"
              >
                Continue
              </v-btn>

              <v-btn text>
                Cancel
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="4">
              <v-card
                class="mb-12"
                color="grey lighten-3"
              >
                <ul>
                  <li>
                    <dl>
                      <dt>
                        • 음식점에 지정결과 최종통보
                      </dt>
                      <dd>
                        - 지정 후 사후관리 등 안내
                      </dd>
                      <dd class="after">
                        <span>사후관리</span>
                        <p>[염도관리] 판매음식의 염도 관리를 위한 주기적인 사후관리 실시</p>
                        <p>[기술지원] 저염식 메뉴 개발을 위한 전문가 기술 지원</p>
                        <p>[홍보지원]나트륨 줄이기 실천음식점 지정 업체의 적극 홍보 진행</p>
                      </dd>
                    </dl>
                  </li>
                  <li>
                    <dl>
                      <dt>• 음식점에 현판 등 배부</dt>
                      <dd>
                        - 사전운영 취소 음식점 경우 염도측정도구 세트 회수
                      </dd>
                    </dl>
                  </li>
                </ul>
              </v-card>

              <v-btn
                color="primary"
                @click="e1 = 1"
              >
                Continue
              </v-btn>

              <v-btn text>
                Cancel
              </v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
        </v-app>
      </div>
    </div>
  </template>


  <script>
  export default {
    data() {
      return{
        e1: 1,
        text: ["뇌졸중", "위장병", "골다공증"],
        img: [require("../assets/sick1.jpg"),require("../assets/sick2.jpg"),require("../assets/sick3.jpg")]
      }
    }
  }
  </script>


  <style>
    img {
      width:100%;
    }
    .row {
      justify-content: center;
    }

    .sub1 {
      width: 100%;
      padding: 50px 0;
      box-sizing: border-box;
    }

    h2 {
      font-size: 3rem;
    }

    dl.about, .recommend, .process {
      width: 80%;
      margin: 0 auto;
    }

    .sub1 h3 {
      font-size: 3rem;
      line-height: 1.5em;
      text-align: left;
    }
    

    dl.about dt h3 span{
      display: block;
    }

    dl.about dd p, .recommend p {
        margin: 20px 0;
    }

    dl.about dd p:nth-child(2n-1), .recommend p:first-of-type {
      font-size: 1.3rem;
      font-weight: 500;
      line-height: 1.8em;
    }

    dl.about dd p:last-child {
      font-weight: 600;
      border-top: 1px dashed #333;
      padding: 20px 0 0
    }

    dl.about dd p:last-child span {
      color: #18a5a0
    }

    dl.about dd p:nth-of-type(2) span {
      color: red;
      text-decoration: underline;
    }
    
    dl.about dd {
        margin: 2rem 0;
    }

    dl.about dd:last-of-type {
      display: flex;
      
    }
    dl.about dd:last-of-type img {
      width: 49%;
    }
    dl.about dd:last-of-type img:first-child {
      margin-right: 2%;
    }

    .recommend {
        margin: 100px auto;
    }

    .recommend .box {
        margin-top: 50px;
    }

    .recommend .box img {
        border-radius: 50%;
        border: 5px solid #18a5a0;
    }

    .recommend p {
        line-height: 2em;
    }

    .recommend .box p {
      text-align: center;
    }

    .process .v-stepper {
      margin-top: 50px;
    }

    .process .v-card {
      padding: 30px 0 10px;
      height: auto;
    }
   
    .v-application p {
        margin-bottom: 10px;
    }

    .v-application {
        font-family: "Noto Sans KR", sans-serif;
    }

    .process li  {
        margin-bottom: 15px;
    }

    .process dt {
        font-size: 1.1em;
        line-height: 1.8em;
        font-weight: 500;
    }

    .process dd span {
        font-size: 1.1em;
        color: #18a5a0;
        font-weight: 500;
        display: inline-block;
        padding-bottom: 10px;
    }

    .process dd.standard, dd.menu, dd.after {
        width: fit-content;
        border: 1px dashed;
        padding: 10px;
        margin-top: 10px;
        background: #fff;
    }

    .process .v-stepper__label {
    line-height: 1.2em;
    margin-top: -5px;
}
    .process .v-application ul, .v-application ol {
      padding: 0 20px;
}
  </style>